@import 'src/styles/base';
.food-item-container {
  height: 276px;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  .image-section {
    height: 183px;
    position: relative;
    .box-img {
      position: relative;
      display: flex;
      height: 180px;
      img {
        width: 100%;
        object-fit: cover;
      }
      .logo {
        width: 56px;
        height: 56px;
        border-radius: 50%;
        position: absolute;
        left: 1em;
        bottom: 1em;
      }
    }

    .content-image-section {
      position: absolute;
      top: 0;
      display: flex;
      justify-content: space-between;
      height: 100%;
      width: 100%;
      align-items: flex-end;
      padding: 16px;
    }
  }

  .price-section {
    display: flex;
    align-items: center;
    .price {
      color: #ff4355;
      font-size: 18px;
      padding-left: 8px;
      font-family: 'Google Sans', sans-serif;
    }
  }

  .content-section {
    height: 93px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 6px 20px 10px 20px;

    .eVoucher-name {
      font-size: 18px;
      font-family: 'Google Sans', sans-serif;
    }

    .bottom-section {
      display: flex;
      justify-content: space-between;

      .bottom-right {
        .customTitle {
          font-size: 17px;
          font-weight: bold;
        }
      }
    }
  }
}
